.garden {
  height: 50vh;

  background-color: #0c312d;

  /* font-size: 1.5em; */
  color: white;

  border-radius: 20px 20px 0px 0px;

  padding: 1em;
}

.garden--head {
  font-size: 1.2em;
  display: flex;
  justify-content: space-between;

  margin: 1em 0;
}

.plant--container {
  display: flex;
  flex-wrap: wrap;

  width: 95%;

  margin: 0 auto 0 auto;
}

.plant--pair {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #26c065;

  border-radius: 5px;

  margin: 0.25em;

  padding: 0.01em 0.5em;
}

.plant--pair--word {
  font-size: 0.75em;
  color: black;

  margin: 0;
}

.plant--pair--image {
  font-size: 2em;
}
